html {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  min-width: 320px;
  height: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #ffffff;
  background-color: #272727; }

img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  object-fit: cover; }

a {
  text-decoration: none;
  color: inherit; }

.site-container {
  min-height: 100vh;
  display: grid;
  grid-template-rows: min-content 1fr;
  overflow: hidden; }

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer; }

.container {
  margin: 0 auto;
  max-width: 1780px;
  padding: 0 30px;
  width: 100%; }

.visually-hidden {
  position: absolute;
  clip-path: inset(100%);
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip: rect(0 0 0 0); }

@media (max-width: 1023px) {
  .container {
    padding: 0 49px; } }

@media (max-width: 767px) {
  .container {
    padding: 0 18px; } }

.main-header__container {
  display: flex;
  align-items: center;
  padding-top: 28px;
  padding-bottom: 28px; }

.main-header__menu {
  display: none; }

.main-header__logo {
  flex-shrink: 0; }

.main-header__list {
  margin: 0;
  margin-left: 76px;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(7, auto);
  gap: 10px 42px; }

@media (max-width: 1530px) {
  .main-header__list {
    grid-template-columns: repeat(5, auto); } }

@media (max-width: 1330px) {
  .main-header__list {
    grid-template-columns: repeat(4, auto); } }

@media (max-width: 1230px) {
  .main-header__list {
    grid-template-columns: repeat(3, auto); } }

@media (max-width: 1130px) {
  .main-header__list {
    grid-template-columns: repeat(2, auto); } }

.main-header__link {
  display: block;
  position: relative;
  font-weight: 700;
  line-height: 150%;
  text-transform: uppercase; }
  .main-header__link::before {
    content: "";
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 0;
    height: 2px;
    background-color: #ffffff;
    transition: all 0.3s ease-in-out; }

.main-header__link:hover::before,
.main-header__link:focus::before,
.main-header__link:active::before {
  width: 100%;
  transition: all 0.3s ease-in-out; }

.main-header__user-navigation {
  flex-shrink: 0;
  margin-left: auto;
  margin-right: 19px; }

@media (max-width: 1023px) {
  .main-header__container {
    position: relative;
    padding-top: 30px;
    padding-bottom: 20px;
    z-index: 100; }
    .main-header__container::before {
      position: absolute;
      z-index: 100;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #272727; }
  .main-header__menu {
    display: block;
    padding: 0;
    z-index: 103;
    transition: all 0.3s ease-in-out; }
    .main-header__menu:hover, .main-header__menu:focus, .main-header__menu:active {
      opacity: 0.5;
      transition: all 0.3s ease-in-out; }
  .main-header__logo {
    margin-left: 90px;
    z-index: 103; }
  .main-header__list {
    z-index: 1;
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    margin: 0;
    padding-top: 163px;
    padding-left: 49px;
    display: block;
    overflow: auto;
    transition: all 0.3s ease-in-out; }
    .main-header__list--nojs {
      left: 0; }
  .main-header__list.active {
    left: 0;
    transition: all 0.3s ease-in-out; }
  .main-header__item:not(:last-child) {
    margin-bottom: 15px; }
  .main-header__link {
    font-size: 36px;
    color: #272727;
    text-shadow: 1px 1px #808080, 1px -1px #808080, -1px 1px #808080, -1px -1px #808080, 3px 3px 6px rgba(0, 0, 0, 0.5); }
    .main-header__link::before {
      display: none; }
    .main-header__link:hover, .main-header__link:focus, .main-header__link:active {
      color: #ffffff;
      text-shadow: none;
      transition: all 0.3s ease-in-out; } }

@media (max-width: 767px) {
  .main-header__container {
    padding-top: 19px; }
  .main-header__menu {
    margin-left: -2px; }
  .main-header__logo {
    margin-left: auto;
    margin-right: -3px;
    width: 100px;
    height: 38px; }
  .main-header__list {
    padding-top: 71px;
    padding-left: 18px; }
  .main-header__link {
    font-size: 20px; } }

.user-navigation {
  display: flex;
  align-items: center; }

.user-navigation__phone {
  width: 73px;
  height: 60px;
  border: 1px solid #ffffff;
  opacity: 0.5;
  transform: skew(-18deg, 0deg);
  transition: all 0.3s ease-in-out; }
  .user-navigation__phone:hover, .user-navigation__phone:focus, .user-navigation__phone:active {
    opacity: 1;
    transition: all 0.3s ease-in-out; }

.user-navigation__phone svg {
  transform: skew(18deg, 0deg); }

.user-navigation__submit {
  margin-left: 33px;
  padding-top: 23px;
  padding-bottom: 22px;
  padding-right: 68px;
  padding-left: 68px; }

.user-navigation__submit span {
  display: block;
  transform: skew(18deg, 0deg); }

@media (max-width: 1023px) {
  .user-navigation {
    z-index: 103; }
  .user-navigation__submit {
    margin-left: 9px;
    padding-right: 28px;
    padding-left: 28px; }
    .user-navigation__submit::after {
      right: -10px; } }

@media (max-width: 767px) {
  .user-navigation {
    position: fixed;
    bottom: 0;
    left: -100%;
    width: 100%;
    margin: 0;
    padding-left: 18px;
    padding-right: 18px;
    padding-bottom: 21px;
    flex-direction: column;
    transition: all 0.3s ease-in-out; }
    .user-navigation--nojs {
      left: 0; }
  .user-navigation.active {
    left: 0;
    transition: all 0.3s ease-in-out; }
  .user-navigation__phone {
    width: 95%;
    margin-bottom: 5px; }
  .user-navigation__submit {
    margin-left: 0;
    width: 95%; }
    .user-navigation__submit::after {
