.promo {
  position: relative; }

.promo__slide {
  position: relative;
  padding-top: 599px;
  padding-bottom: 169px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-top-left-radius: 60px;
  border-top-right-radius: 60px; }
  .promo__slide--first {
    background-image: url("../img/bg-1.jpg"); }
    @media (min-resolution: 2dppx) {
      .promo__slide--first {
        background-image: url("../img/bg-1@2x.jpg"); } }
  .promo__slide--second {
    background-image: url("../img/bg-2.jpg"); }
    @media (min-resolution: 2dppx) {
      .promo__slide--second {
        background-image: url("../img/bg-2@2x.jpg"); } }
  .promo__slide--third {
    background-image: url("../img/bg-3.jpg"); }
    @media (min-resolution: 2dppx) {
      .promo__slide--third {
        background-image: url("../img/bg-3@2x.jpg"); } }
  .promo__slide--hidden {
    display: none; }
  .promo__slide::after {
    position: absolute;
    content: "";
    bottom: 0;
    height: 170px;
    width: 100%;
    background: linear-gradient(180deg, #131313 0%, rgba(19, 19, 19, 0) 100%);
    transform: rotate(-180deg); }

.promo__content {
  position: relative;
  padding-left: 100px;
  padding-right: 100px; }

.promo__list {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 0;
  padding: 0;
  padding-bottom: 159px;
  padding-right: 95px;
