  padding-top: 23px;
  padding-bottom: 22px;
  padding-right: 68px;
  padding-left: 68px; }

.promo__submit span {
  display: block;
  transform: skew(18deg, 0deg); }

@media (max-width: 1023px) {
  .promo__content {
    padding-left: 49px;
    padding-right: 49px; }
  .promo__list {
    padding-right: 49px; } }

@media (max-width: 767px) {
