.btn {
  position: relative;
  font-weight: 700;
  font-size: 12px;
  color: inherit;
  text-transform: uppercase;
  border: 1px solid rgba(255, 255, 255, 0.5);
  transform: skew(-18deg, 0deg);
  background-color: #c2ab81;
  opacity: 1;
  transition: all 0.3s ease-in-out; }
  .btn::after {
    position: absolute;
